.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    /* color: #f2f2f2; */
    color: black;
    padding: 24px 10px;
    margin: auto;
}

.nav {
  overflow: hidden;
  /* background-color: #00acb0; */
  background-color: #ffe01b;
}

.nav-link {
  float: left;
  /* color: #f2f2f2; */
  /* this color below is a dark blue */
  /* background-color: #007c89; */
  background-color: #ffe01b;
  color: black;
  text-align: center;
  padding: 24px 10px;
  text-decoration: none;
  font-size: 15px;

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  background-color: #008183;
}

.nav-right {
  float: right;
}
